/////////////////////////////////////////////////////////////////////////////////////////////////////////
// REPEATER TEAM
/////////////////////////////////////////////////////////////////////////////////////////////////////////


.b-repeater-team {

  .body {
    background: url(pattern-stripe-light-tight.png);
    // background: repeating-linear-gradient(
    //   -45deg,
    //   transparent,
    //   $grey5 1px,
    //   transparent 2px,
    //   transparent 6px
    // );
    margin: 0 0 8px;
    padding: 0 22px;
    position: relative;
  }

  figure {
    @include aspect-ratio-img(1,1);
    width: 100%;
    height: 100%;
    z-index: 1;
    border-radius: 50%;
    background: red;
    overflow: hidden;
  }


  .header{
    position: absolute;
    top: 22px;
    left: 12px;
    z-index: 2;

    &-name{
      color: $yellow;
      font-size: 30px;
      margin: 0 0 5px;
    }
    &-title{
      font-size: 14px;
      margin: 0;
    }
  }

  a {
    text-decoration: none;
    color: inherit;
  }

  @media screen and (min-width: $bp-768) {


  }

}

